import React from 'react';
import styled from 'styled-components';
import blend from '../images/blend.png';
import {motion} from 'framer-motion';

const WidgetForm = () => {
   return(
       <Section>
            <header>
                <img src={blend} alt="blend logo"/>
            </header>
            <div className="line"></div>
             <Form>
                <select name="blend-widgets-dropdown" id="blend-widgets-dd">
                <option value="Trending Content">Trending Content</option>
                <option value="Upcoming Events">Upcoming Events</option>
                </select> 
                <Button type="submit">Submit</Button>  
            </Form>
       </Section>
   )
}

export default WidgetForm;

const Section = styled.section`
    display:flex;
    align-items:center;
    justify-content: center;
    flex-direction: column;
    border: 1px solid #2A2A2A;
    width: 50%;
    margin:auto;
    margin-top: 10rem;
    padding: 2rem 1rem;
    background: white;
    img{
        width: 100px;
    }
    .line{
        width: 50vw;
        margin-top: 2rem;
        height: 0.1rem;
        background: #2A2A2A;
    }
`;

const Form = styled.form`
    display:flex;
    flex-direction:column;
    height: 20vh;
    align-items: center;
    justify-content: center;
    select{
        width: 20vw;
        text-align:center;
        padding: 1rem;
        margin: 1rem 1rem;
    }
    }
`;

const Button = styled(motion.button)`
        width: 5vw;
        background: #FF2525;
        border:none;
        padding: 1rem 2rem;
        border-radius: 15px;
        color: white;
        &:hover{
            background: #FF4025;
`;
