import React from 'react';
import {motion} from 'framer-motion';
import styled from 'styled-components';

const TrendingContent = () => {
    return(
        <motion.div drag
        dragConstraints={{
          top: -500,
          left: -500,
          right: 500,
          bottom: 500,
        }}>
            <Button>Click to drag</Button>
            <Iframe src="https://lxpzjv29avwc6.hana.ondemand.com/lxp/cc/widget/trendingTopicWidget?maxPageSize=4" width="1000px" height="400px" />
        </motion.div>
    )
}

export default TrendingContent;

const Button = styled.button`
    width: 5vw;
        background: #FF2525;
        border:none;
        padding: 1rem 2rem;
        border-radius: 15px;
        color: white;
        display:block;
        &:hover{
            background: #FF4025;

`;

const Iframe = styled(motion.iframe)`
        border:none;
`